import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const state = {
  stepName: ['', '', ''],
  steps: [1, 2, 3, 4, 5, 6],
  step: 1,
  description: ['', ',', '', '', '', ''],
  radioStep1: '',
  form: {
    company: '',
    name: '',
    state: '',
    sizeAndCapacitys: '',
    categorys: [],
    unit: '',
    type: [],
    voltagelevel: '',
    place: '',
    major: [],
    designState: '',
    startTime: '',
    endTime: '',
    character: '',
    performanceReq: '',
    lowPrice: '',
    highPrice: '',
    priceType: '',
    aptitude: '',
    paymentMethods: '',
    isAcceptJointBid: 'true',
    stateUnits: [
      { state: '', endTime: '', requireResult: '' },
      { state: '', endTime: '', requireResult: '' },
      { state: '', endTime: '', requireResult: '' }
    ],
  },
  qualificationRequirements: { CET: '' },
  projectCharacteristics: { difficulty: '', price: '' },
  unit: ['km', 'm', 'cm', 'dm', ''],
  existingData: { detail: '' },
  states: ['', '', ''],
  major: ['', '', '', ''],
  designState: ['', '', '', '', '', '', ''],
  sizeAndCapacitys: ['MW', 'KW', 'kVA'],
  categorys: ['', '', '', '', '', ''],
  type: ['', '', '', '', ''],
  voltagelevel: ['800KV', '500KV', '690V', '1000KV', '750KV', '500KV', '330KV', '220KV', '110KV', '66KV', '35KV', '20KV', '10KV', '6KV', '380V', '220V', ],
  character: ['', ''],
  performanceReq: ['', ''],
  priceType: ['', ''],
  aptitude: ['', ''],
  paymentMethods: ['', '']
}

const mutations = {
  init(state) {
    state.step = 1
  },
  init2(state) {
    state.step = 2
  },
  init3(state) {
    state.step = 3
  },
  init4(state) {
    state.step = 4
  },
  init5(state) {
    state.step = 5
  },
  init6(state) {
    state.step = 6
  },
  addStateUnits(state) {
    state.form.stateUnits.push({ state: '', endTime: '', requireResult: '' })
  },
  myDelete(state, el) {
    state.form.stateUnits = state.form.stateUnits.filter(o => o.state !== el.state)
  }
}
export default new Vuex.Store({
  state,
  mutations
})



